@use "../../vars.scss" as *;
@use "../../assets/shadows/MaterialShadows.scss" as shadow;

.task-item {
    background: $color-bg-2;

    @include flex-left();
    width: 100%;

    padding: 10px 7px;
    gap: 7px 0;

    border-radius: $rounding;

    @include shadow.shadow-2dp()
}

.completed-item {
    background: $color-bg;
    color: $color-text-2;

    box-shadow: none;
}

.completed-item svg {
    fill: $color-text-2;
}